.section__bonus-description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.section__bonus-description .bonus__description-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  padding: 25px;
  margin-bottom: 20px;
  border-radius: 2em;
  max-height: 550px;
  font-size: 40px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.section__bonus-description .bonus__description-item:last-child {
  margin-bottom: 0;
}

.section__bonus-description .bonus__description-item .description__image-wrapper {
  width: calc(100% / 3 - 2 * 1em - 2 * 1px);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 10px;
  margin-right: 80px;
  flex-basis: 20%;
}

.section__bonus-description .bonus__description-item img {
  width: auto;
  height: 100%;
  object-fit: contain;
  display: flex;
  align-items: center;
  justify-content: center;
}

.description__text-wrappper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 80%;
}

.description__text-wrappper p {
  font-size: 40px;
  padding-right: 10px;
}

.section__description-rule {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}

.section__description-rule a {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: underline;
}

.section__description-rule a:hover {
  color: #04081d;
}
